.specialcard {
  border-radius: 16px;
  width: 540px;
  height: 540px;

  /* background: url(https://firebasestorage.googleapis.com/v0/b/update-image.appspot.com/o/images%2FIMG_20200330_110953_746.jpg?alt=media&token=53490ca2-3aad-4a1e-93c6-c7d7ad8b65a6);
   */
  position: relative;
  background-size: contain;
  overflow: hidden;

  box-shadow: 0 5px 50px rgba(0, 0, 0, 0.85);
}
.specialcard:before {
  content: "";
  position: absolute;

  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7);
}
.specialcard .img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
}
.specialcard .img span {
  width: 25%;
  height: 100%;
  /* background: url(https://firebasestorage.googleapis.com/v0/b/update-image.appspot.com/o/images%2FIMG_20200330_110953_746.jpg?alt=media&token=53490ca2-3aad-4a1e-93c6-c7d7ad8b65a6); */
  transition: 0.5s;
  background-size: cover;
}
.specialcard .img span:nth-child(1) {
  background-position: 0;
  transition-delay: 0;
}
.specialcard .img span:nth-child(2) {
  background-position: 20%;
  transition-delay: 0.1s;
}
.specialcard .img span:nth-child(3) {
  background-position: 40%;
  transition-delay: 0.2s;
}
.specialcard .img span:nth-child(4) {
  background-position: 60%;
  transition-delay: 0.3s;
}
.specialcard .img span:nth-child(5) {
  background-position: 80%;
  transition-delay: 0.2s;
}
.specialcard .img span:nth-child(6) {
  background-position: 100%;
  transition-delay: 0.3s;
}

.specialcard:hover .img > span {
  transform: translateY(-100%);
}
.specialcard:hover .specialcontent {
  transform: translateY(0%);
  transition: 1s;
  transition-delay: 0.1s;
}

.specialcontent {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  padding: 20px 20px;
  width: 100%;
  height: 100%;
  transform: translateY(100%);
}
.specialcardHoverText {
  font-size: 2.5rem;
}
.specialcardHead1 {
  margin-top: 10px;
  margin-bottom: 0;
  color: #ffffff;
  font-size: 3.5rem;
}
.specialcardHead2 {
  margin-top: 15px;
  margin-bottom: 20px;
  font-family: "East Sea Dokdo cursive";
  color: #ffffff;
  font-size: 3rem;
}
.specialcardPara {
  text-align: justify;
  font-family: "East Sea Dokdo cursive";
  color: #ffffff;
  font-size: 2rem;
}
@media only screen and (max-width: 400px) {
  .specialcard {
    width: 340px;
    height: 340px;
  }
  .specialcardHoverText {
    font-size: 1.75rem;
  }
  .specialcardHead1 {
    font-size: 2.5rem;
  }
  .specialcardHead2 {
    margin-top: 7px;
    margin-bottom: 10px;
    font-size: 2.25rem;
  }
  .specialcardPara {
    font-size: 1.3rem;
  }
}
@media only screen and (min-width: 400px) and (max-width: 500px) {
  .specialcard {
    width: 380px;
    height: 380px;
  }
  .specialcardHoverText {
    font-size: 2rem;
  }
  .specialcardHead1 {
    font-size: 3rem;
  }
  .specialcardHead2 {
    margin-top: 10px;
    margin-bottom: 15px;
    font-size: 2.5rem;
  }
  .specialcardPara {
    font-size: 1.5rem;
  }
}
@media only screen and (min-width: 500px) and (max-width: 600px) {
  .specialcard {
    width: 480px;
    height: 480px;
  }
}
@media only screen and (min-width: 700px) and (max-width: 800px) {
  .specialcard {
    width: 640px;
    height: 640px;
    margin-left: -40px;
  }
  .specialcardHoverText {
    font-size: 3rem;
  }
  .specialcardHead1 {
    font-size: 4rem;
  }
  .specialcardHead2 {
    font-size: 3.5rem;
  }
  .specialcardPara {
    font-size: 2.5rem;
  }
}
@media only screen and (min-width: 800px) and (max-width: 990px) {
  .specialcard {
    width: 730px;
    height: 730px;
  }
  .specialcardHoverText {
    font-size: 3.5rem;
  }
  .specialcardHead1 {
    font-size: 4.5rem;
  }
  .specialcardHead2 {
    font-size: 4rem;
  }
  .specialcardPara {
    font-size: 3rem;
  }
}

